const { device, expect, element, by, waitFor } = require('detox');

describe('JoudEnglishApp - Exercise Flows E2E', () => {
  beforeEach(async () => {
    await device.reloadReactNative();
    
    // Navigation commune vers les exercices
    await waitFor(element(by.id('dashboard-container')))
      .toBeVisible()
      .withTimeout(10000);
    
    // Aller aux exercices (gestion des deux cas : nouveau/existant utilisateur)
    try {
      await element(by.id('level-selection-button')).tap();
      await element(by.id('level-1')).tap();
    } catch (e) {
      // Utilisateur existant, chercher un autre moyen d'accéder aux exercices
      try {
        await element(by.text('Exercices')).tap();
      } catch (e2) {
        console.log('Could not navigate to exercises, skipping test');
        return;
      }
    }
    
    await waitFor(element(by.id('exercise-selection-container')))
      .toBeVisible()
      .withTimeout(5000);
  });

  describe('Vocabulary Exercise Flow', () => {
    it('should complete a vocabulary exercise session', async () => {
      // Sélectionner vocabulaire
      await element(by.id('vocabulary-exercise')).tap();
      
      // Attendre le chargement
      await waitFor(element(by.text('Vocabulaire')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Vérifier les éléments de l'exercice
      await waitFor(element(by.id('vocabulary-word')))
        .toBeVisible()
        .withTimeout(5000);
      
      // Révéler la traduction
      try {
        await element(by.id('reveal-translation-button')).tap();
        await waitFor(element(by.id('vocabulary-translation')))
          .toBeVisible()
          .withTimeout(3000);
      } catch (e) {
        console.log('Translation reveal not available');
      }
      
      // Passer plusieurs mots
      for (let i = 0; i < 3; i++) {
        try {
          await element(by.id('next-word-button')).tap();
          await waitFor(element(by.text(`${i + 2}/`)))
            .toBeVisible()
            .withTimeout(3000);
        } catch (e) {
          console.log(`Could not advance to word ${i + 2}`);
          break;
        }
      }
      
      // Vérifier la sauvegarde d'activité
      try {
        await element(by.id('save-activity-button')).tap();
      } catch (e) {
        console.log('Save activity button not found');
      }
    });

    it('should handle vocabulary exercise completion', async () => {
      await element(by.id('vocabulary-exercise')).tap();
      
      await waitFor(element(by.text('Vocabulaire')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Simuler la completion rapide (si possible)
      try {
        // Chercher un bouton "Terminer" ou "Finir"
        await waitFor(element(by.text('Terminer')).or(by.text('Finir')))
          .toBeVisible()
          .withTimeout(3000);
        
        await element(by.text('Terminer').or(by.text('Finir'))).tap();
        
        // Vérifier l'écran de résultats
        await waitFor(element(by.text('Résultats')).or(by.text('Félicitations')))
          .toBeVisible()
          .withTimeout(5000);
      } catch (e) {
        console.log('Exercise completion flow not available');
      }
    });
  });

  describe('Spelling Exercise Flow', () => {
    it('should interact with spelling exercise', async () => {
      await element(by.id('spelling-exercise')).tap();
      
      await waitFor(element(by.text('Orthographe')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Vérifier le champ de saisie
      await waitFor(element(by.id('spelling-input')))
        .toBeVisible()
        .withTimeout(5000);
      
      // Taper du texte
      try {
        await element(by.id('spelling-input')).typeText('hello');
        
        // Vérifier le bouton de validation
        await waitFor(element(by.id('check-spelling-button')))
          .toBeVisible()
          .withTimeout(3000);
        
        await element(by.id('check-spelling-button')).tap();
        
        // Vérifier le feedback
        await waitFor(element(by.id('spelling-feedback')))
          .toBeVisible()
          .withTimeout(3000);
      } catch (e) {
        console.log('Spelling interaction not available');
      }
    });
  });

  describe('Reading Exercise Flow', () => {
    it('should display and interact with reading content', async () => {
      await element(by.id('reading-exercise')).tap();
      
      await waitFor(element(by.text('Lecture')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Vérifier le texte de lecture
      await waitFor(element(by.id('reading-text')))
        .toBeVisible()
        .withTimeout(5000);
      
      // Scroll dans le texte
      try {
        await element(by.id('reading-text')).scroll(200, 'down');
        await element(by.id('reading-text')).scroll(200, 'up');
      } catch (e) {
        console.log('Reading scroll not available');
      }
      
      // Chercher des questions de compréhension
      try {
        await waitFor(element(by.id('reading-questions')))
          .toBeVisible()
          .withTimeout(3000);
        
        // Répondre à une question
        await element(by.id('answer-option-1')).tap();
        
        await waitFor(element(by.id('question-feedback')))
          .toBeVisible()
          .withTimeout(3000);
      } catch (e) {
        console.log('Reading questions not available');
      }
    });
  });

  describe('Word Games Exercise Flow', () => {
    it('should interact with word games', async () => {
      await element(by.id('word-games-exercise')).tap();
      
      await waitFor(element(by.text('Jeux de mots')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Vérifier les éléments de jeu
      await waitFor(element(by.id('game-element')))
        .toBeVisible()
        .withTimeout(5000);
      
      // Interactions de jeu
      try {
        await element(by.id('game-element')).tap();
        
        // Vérifier le score ou feedback
        await waitFor(element(by.id('game-score')))
          .toBeVisible()
          .withTimeout(3000);
      } catch (e) {
        console.log('Game interaction not available');
      }
    });
  });

  describe('Exercise Navigation and State', () => {
    it('should maintain state when navigating between exercises', async () => {
      // Commencer vocabulaire
      await element(by.id('vocabulary-exercise')).tap();
      await waitFor(element(by.text('Vocabulaire')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Faire une action
      try {
        await element(by.id('next-word-button')).tap();
      } catch (e) {
        console.log('Could not advance word');
      }
      
      // Retourner à la sélection
      await element(by.id('back-button')).tap();
      await waitFor(element(by.id('exercise-selection-container')))
        .toBeVisible()
        .withTimeout(5000);
      
      // Aller à un autre exercice
      await element(by.id('spelling-exercise')).tap();
      await waitFor(element(by.text('Orthographe')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Retourner à vocabulaire
      await element(by.id('back-button')).tap();
      await element(by.id('vocabulary-exercise')).tap();
      
      // Vérifier que l'état est maintenu (mot 2 au lieu de 1)
      try {
        await waitFor(element(by.text('2/')))
          .toBeVisible()
          .withTimeout(3000);
        console.log('State maintained correctly');
      } catch (e) {
        console.log('State not maintained or different UI structure');
      }
    });

    it('should handle exercise interruption and resume', async () => {
      // Commencer un exercice
      await element(by.id('vocabulary-exercise')).tap();
      await waitFor(element(by.text('Vocabulaire')))
        .toBeVisible()
        .withTimeout(8000);
      
      // Faire quelques actions
      try {
        await element(by.id('next-word-button')).tap();
        await element(by.id('next-word-button')).tap();
      } catch (e) {
        console.log('Could not advance words');
      }
      
      // Simuler interruption (retour au dashboard)
      await device.reloadReactNative();
      
      // Vérifier que l'activité peut être reprise
      await waitFor(element(by.id('dashboard-container')))
        .toBeVisible()
        .withTimeout(10000);
      
      try {
        await waitFor(element(by.id('continue-activity-button')))
          .toBeVisible()
          .withTimeout(3000);
        
        await element(by.id('continue-activity-button')).tap();
        
        // Vérifier qu'on reprend où on s'était arrêté
        await waitFor(element(by.text('Vocabulaire')))
          .toBeVisible()
          .withTimeout(8000);
      } catch (e) {
        console.log('Resume functionality not available');
      }
    });
  });

  describe('Exercise Performance and Loading', () => {
    it('should load exercises within acceptable time limits', async () => {
      const exerciseIds = [
        'vocabulary-exercise',
        'spelling-exercise', 
        'reading-exercise',
        'phrases-exercise'
      ];
      
      for (const exerciseId of exerciseIds) {
        const startTime = Date.now();
        
        try {
          await element(by.id(exerciseId)).tap();
          
          // Attendre que l'exercice se charge (max 10 secondes)
          await waitFor(element(by.id('activity-indicator')))
            .not.toBeVisible()
            .withTimeout(10000);
          
          const loadTime = Date.now() - startTime;
          console.log(`${exerciseId} loaded in ${loadTime}ms`);
          
          // Retourner à la sélection
          await element(by.id('back-button')).tap();
          await waitFor(element(by.id('exercise-selection-container')))
            .toBeVisible()
            .withTimeout(5000);
            
        } catch (e) {
          console.log(`${exerciseId} failed to load within time limit`);
        }
      }
    });
  });
});